<template>
    <div class="card">
        <form class="form_search_post" @submit.prevent="createCategory">
            <div class="form-group">
                <label for="exampleInputEmail1">Создать</label>
                <input type="text" class="form-control" id="exampleInputEmail1" v-model.lazy="$v.categoryname.$model">
                <small  class="btn form-text bg-danger text-light" v-if="$v.categoryname.$dirty && !$v.categoryname.maxLength">Название категории не должно быть больше 15 символов.</small>
            </div>
            <button type="submit" class="btn botton--green">Отправить</button>
        </form>
    </div>
</template>

<script>
import {  maxLength  } from 'vuelidate/lib/validators'

export default {

    data() {
      return {
        categoryname: '',
        loading: true,

      }
    },
    validations: {
        categoryname: {
          maxLength : 15
        },

    },
    methods: {
        async createCategory() {
            console.log("jujhjl")
            if(this.$v.$invalid) {
                this.$v.$touch();
                return
            }
            else {
                try{ 
                    await this.$store.dispatch("createCategory", {categoryname: this.categoryname})

                    this.categoryname = '';
                    this.$v.$reset();
                    await this.$store.dispatch("Massage", "Категоря создана успешно. ");
             
                }
                catch(e) {
                    console.log("errorl create post: "+ e);
                }
            }
        },
    }
}
</script>

