<template>
    <div class="card">

        <form  v-if="loading"  class="card form_search_post">
            <div class="form-group">
            <label for="categoryOptions">Выберите категорию для редактирования.</label>
            <b-form-select  v-model="selected" class="form-control" id="categoryOptions">
                <b-form-select-option 
                v-for="category of categoryList"
                :key="category.id"
                :value="category.id"
                >
                {{category.categoryname}}
                </b-form-select-option>
            </b-form-select >
            </div>
        </form>

        <Loader v-else></Loader>
        <form @submit.prevent="updageCategory" class="form_search_post">
            <div class="form-group">
                <label for="categoryEdit">Редактировать</label>
                <small  class="btn form-text bg-danger text-light" v-if="$v.category_update.$dirty && !$v.category_update.maxLength">Название категории не должно быть больше 15 символов.</small>
                <small  class="btn form-text bg-danger text-light" v-if="$v.category_update.$dirty && !$v.category_update.required">Нельзя создать пустую категорию.</small>
                <input  class="form-control" id="categoryEdit" v-model.lazy="$v.category_update.$model">
             </div>
            <button type="submit"  class="btn botton--green">Обновить</button>
        </form>
    </div>
</template>


<script>
    import {  maxLength, required  } from 'vuelidate/lib/validators'
    import Loader from '@/components/blocks/Loaders/Loader.vue'


  export default {
    components: {
        Loader
    },
    data() {
      return {
        categoryList: [],
        loading: true,
        category_update: '',
        selected: ''
      }
    },
    validations: {
        category_update: {
            required,
            maxLength  
        }
    },
    async mounted() {
        this.categoryList = await this.$store.dispatch("fatchCategory");
    },
    watch: {
          selected(value_id) {
          const {categoryname} = this.categoryList.find(c => c.id === value_id);

          this.category_update = categoryname

       }
    },
    methods: {
        async updageCategory () {
             if(this.$v.$invalid) {
                this.$v.$touch();
                return
            }
            else {
                try{ 

                    await this.$store.dispatch("updateCategory", {name: this.category_update, id: this.selected})

                    this.category_update = '';
                    this.$v.$reset();
                    await this.$store.dispatch("Massage", "Категоря обновленна успешно. ");

                    this.categoryList = await this.$store.dispatch("fatchCategory");
                }
                catch(e) {
                    console.log("errorl create post: "+ e);
                }
            }
        },
    }
}
</script>

